.products {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  min-width: 300px;
  min-height: 400px;
  text-align: center;
  border: 1px solid #eee;
  /* border-radius: 10px; */
  overflow: hidden;
  padding: 10px;
  box-shadow: 1px 2px 10px #ddd;
  margin: 10px;
  transition: all 0.3s;
  margin-bottom: 40px;
}

.card:hover {
  transform: translateY(-5px);
}

.card img {
  width: 100%;
  height: 100%;
  max-width: 300px;
  max-height: 300px;
  display: block;
  object-fit: contain;
}

.card h3 {
  font-size: 15px;
  margin: 10px 0;
}

.card h3 a {
  text-decoration: none;
  color: #333;
}

.card span {
  color: crimson;
}

.card button {
  border: none;
  outline: none;
  background: #333;
  color: white;
  width: 100%;
  height: 40px;
  display: block;
  cursor: pointer;
  border-radius: 5px;
  /* text-transform: uppercase; */
  letter-spacing: 2px;
  margin: 15px 0;
}

.card .content a {
  text-decoration: none;
}
