.filter {
  width: 100%;
  text-transform: uppercase;
}

.brand {
  margin-top: 30px;
  padding: 3px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.brand ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.brand ul li {
  list-style: none;
  bottom: #eee;
  padding: 10px 20px;
  margin: 6px;
  letter-spacing: 1px;
  cursor: pointer;
  transition: 0.2s;
  border-radius: 10px;
}

.brand ul li:hover {
  background: #7c7c7c;
  color: #fff;
}

.brand ul li.active {
  background: #7c7c7c;
  color: #fff;
}

.filter .seach {
  height: 30px;
}
