.loginForm {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginInfo {
  width: 500px;
}

.loginForm .loginInfo h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 50px;
  font-size: 50px;
}

.loginForm .loginInfo .loginInput h3 {
  font-size: 16px;
}

.loginForm .loginInfo .loginInput input {
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  margin-bottom: 12px;
  padding-left: 10px;
  border-radius: 5px;
}

.check {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.check .checkbox {
  margin-right: 120px;
}

.check .checkbox input {
  margin-right: 4px;
}

.check a {
  text-decoration: none;
  color: black;
}

.check > span:hover,
a:hover {
  color: #ccc;
  cursor: pointer;
}

.loginBtn {
  margin: 30px 0 20px 0;
  border: 1px solid #ccc;
  width: 100%;
  border-radius: 5px;
  background: #333;
  color: white;
  padding: 10px 10px;
  font-size: 18px;
  cursor: pointer;
}

.or {
  color: #777;
  text-align: center;
  font-size: 18px;
  margin-bottom: 20px;
}

.GoogleBtn,
.FacebookBtn,
.TwitterBtn {
  background: white;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 6px;
  border: 1px solid #ccc;
  width: 100%;
  border-radius: 5px;
  padding: 10px 10px;
  font-size: 20px;
  cursor: pointer;
  font-size: 18px;
  color: #555;
}

.GoogleBtn img,
.FacebookBtn img,
.TwitterBtn img {
  width: 30px;
  margin-left: 50px;
  margin-right: 80px;
}

.TwitterBtn {
  margin-bottom: 60px;
}
