.payment {
  display: flex;
  justify-content: center;
  align-items: center;
}

.memberInfo {
  width: 800px;
}

.payment .memberInfo h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 50px;
  font-size: 50px;
}

.payment .memberInfo .customer {
  display: flex;
  margin-bottom: 30px;
  align-items: center;
  border-bottom: 1px solid #ccc;
  padding-bottom: 30px;
}

.payment .memberInfo .customer h3 {
  font-weight: bold;
  margin-right: 20px;
  margin-bottom: 0;
}

.payment .memberInfo .customer button {
  font-size: 12px;
  padding: 3px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: white;
  cursor: pointer;
  margin-top: 5px;
}

.payment .memberInfo .delivery h3 {
  font-weight: bold;
  margin-bottom: 20px;
}

.payment .memberInfo .delivery .label {
  display: block;
}

.payment .memberInfo .delivery .box {
  display: flex;
}

.payment .memberInfo .delivery .box .box1 {
  display: block;
  width: 50%;
  margin-right: 10px;
}

.payment .memberInfo .delivery .box .box2 {
  display: block;
  width: 50%;
}

.payment .memberInfo .delivery .form-control1 {
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  margin-bottom: 12px;
  padding-left: 10px;
  border-radius: 5px;
}

.payment .memberInfo .delivery .form-control {
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  margin-bottom: 12px;
  padding-left: 10px;
  border-radius: 5px;
}

.address {
  display: flex;
  align-items: center;
}

.address .form-control2 {
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  margin-bottom: 0;
  padding-left: 10px;
  border-radius: 5px;
}

.address button {
  margin-left: 3px;
  width: 12%;
  height: 40px;
  border: 1px solid #ccc;
  background: #333;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.seachAddress {
  display: flex;
  justify-content: center;
  align-items: center;
}

.seachAddress input {
  margin-bottom: 0;
}

.addressInput {
  margin-top: 4px;
}

.goCartBtn {
  border-bottom: 1px solid #ccc;
  padding-bottom: 30px;
}

.goCartBtn button {
  margin-left: 3px;
  width: 12%;
  height: 40px;
  border: 1px solid #ccc;
  background: #333;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.productInfo {
  margin-top: 30px;
}

.productInfo .product {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.productInfo .box {
  display: flex;
  margin-bottom: 40px;
}

.productInfo .box .box1 img {
  display: block;
  width: 120px;
  margin: 0 150px 0 150px;
}

.productInfo .box .box2 {
  display: block;
}

.productInfo .box .box2 h2 {
  font-size: 16px;
  margin-bottom: 5px;
}

.productInfo .box .box2 p {
  font-size: 14px;
  margin-bottom: 5px;
}

.productInfo .box .box2 .count {
  display: block;
  font-size: 14px;
  margin-bottom: 3px;
}

.productInfo .box .box2 .price {
  font-size: 14px;
}

.paymentInfo {
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid #ccc;
  padding-bottom: 30px;
}

.paymentInfo .paymentAmount {
  margin-right: 30px;
  font-weight: bold;
}

.nextStep .nextStepBtn {
  margin: 30px 0 60px 0;
  border: 1px solid #ccc;
  width: 100%;
  border-radius: 5px;
  background: #333;
  color: white;
  padding: 10px 10px;
  font-size: 18px;
  cursor: pointer;
}
