.details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 50px 0;
}

.details .imgBox {
  display: block;
  position: relative;
}

.details .imgBox img {
  height: 290px;
  display: inline-block;
  object-fit: cover;
  transform: scale(1.4);
  margin-bottom: 80px;
}

.details .imgBox .changeImg {
  position: absolute;
  transform: scale(0.3);
  display: flex;
  left: -550px;
  top: 230px;
  cursor: pointer;
}

.details .imgBox .changeImg img {
  margin-right: 140px;
}

.details .box {
  max-width: 500px;
  min-width: 290px;
  margin: 25px;
}

.details .box .row h2,
.details .box .row p {
  margin-bottom: 15px;
}

.details .box .row p {
  margin-top: 15px;
}

.details .box .row button {
  width: 40px;
  padding: 5px;
  display: inline-block;
  margin-right: 4px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  cursor: pointer;
  background: white;
  border-radius: 5px;
}

.details .box .row button:hover {
  background: #ccc;
}

.details .box .row span {
  color: crimson;
}

.box .addCart {
  text-align: center;
  width: 150px;
  background: #333;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  padding: 10px 25px;
  display: inline-block;
  margin-bottom: 80px;
  margin-right: 10px;
}

.box .buyBtn {
  text-align: center;
  width: 150px;
  background: #333;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  padding: 10px 25px;
  display: inline-block;
  margin-bottom: 80px;
}

.details select {
  display: block;
  width: 200px;
  padding: 10px;
  font-family: inherit;
  margin-bottom: 20px;
}

.shipping {
  border-top: 1px solid #ccc;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: left;
  margin: 80px 100px;
  padding-top: 50px;
}

.shipping .nav-ul {
  display: flex;
  width: 500px;
  margin-bottom: 50px;
}

.shipping .nav-ul .nav-li {
  margin: 0 50px 20px 0;
  list-style: none;
  font-size: 20px;
}

.shipping .nav-ul .nav-li .active {
  padding-bottom: 13px;
  border-bottom: 4px solid #333;
}

.shipping h3 {
  font-size: 20px;
  margin-bottom: 15px;
}

.shipping ul {
  margin-bottom: 20px;
}

.shipping ul li {
  font-size: 17px;
  margin-left: 30px;
  list-style: square;
}

.shipping p {
  font-size: 17px;
}

.shipping .nav a {
  color: #333;
}

.sizeGuide {
  max-width: 700px;
  width: 100%;
}

@media (max-width: 767px) {
  .details .box .row h2 {
    margin-top: 100px;
  }
}
