.cart {
  max-width: 1100px;
  height: 350px;
  display: flex;
  justify-content: space-around;
  padding: 10px;
  margin: 10px auto;
  overflow: hidden;
  /* border: 2px solid rgb(224, 224, 224); */
  border-radius: 10px;
  margin-top: 40px;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 1px 2px 10px #ddd;
}

.cart .imgBox {
  display: block;
}

.cart .imgBox img {
  height: 290px;
  display: inline-block;
  object-fit: cover;
  transform: scale(1.4);
  margin-bottom: 80px;
  margin-left: 50px;
}

.cart .box {
  margin-left: 80px;
  display: flex;
  justify-content: start;
  width: 500px;
}

.cart .box .row h2 {
  margin-top: 30px;
  margin-bottom: 15px;
}
.cart .box .row .price {
  color: crimson;
}

.cart .box .row p {
  margin-top: 20px;
}

.cart .box .row button {
  margin-top: 20px;
  width: 40px;
  padding: 5px;
  display: inline-block;
  margin-right: 4px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  background: white;
}

.cart .box .row button:hover {
  background: #ccc;
}

.cart .box .count .countBtn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  color: black;
  padding: 3px;
  border-radius: 5px;
}

.cart .box .count span {
  padding: 15px;
}

.delete {
  position: absolute;
  top: 14px;
  right: 16px;
  font-size: 20px;
}

.total {
  max-width: 1350px;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 30px 0;
}

.total h3 {
  margin-right: 30px;
  font-size: 16px;
}

.total a {
  background: #333;
  color: white;
  padding: 10px 25px;
  text-decoration: none;
  margin-right: 30px;
  border-radius: 5px;
}

@media (max-width: 767px) {
  .cart {
    width: 90%;
    height: 300px;
  }

  .cart .imgBox img {
    height: 230px;
    transform: scale(0.8);
    margin-left: 20px;
  }

  .cart .box .row {
    margin-left: 25px;
  }

  .cart .box .row h2 {
    font-size: 16px;
  }
  .cart .box .row .price {
    font-size: 14px;
  }

  .cart .box .row p {
    font-size: 14px;
  }

  .cart .box .row button {
    width: 30px;
    font-size: 12px;
  }

  .delete {
    top: 14px;
    right: 10px;
  }
}
