header {
  min-height: 70px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
  background: #333;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: 0 0 5px black;
}

.header-logo a {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
}

header nav {
  display: flex;
}

header nav ul li {
  display: inline-block;
  list-style: none;
  padding-right: 10px;
}

header nav ul li a {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  padding: 5px 15px;
}

header nav ul li a:hover {
  transition: all 0.2s ease-out;
  border-bottom: 3px solid white;
}

.header-cart {
  cursor: pointer;
  position: relative;
}

.header-cart span {
  position: absolute;
  top: -15px;
  right: -12px;
  background: dodgerblue;
  font-size: 12px;
  color: white;
  padding: 3px 6px;
  border-radius: 50%;
  z-index: 999;
}

.cartIcon {
  color: white;
  transform: scale(1.2);
}

.header-menu,
.header-close {
  cursor: pointer;
  display: none;
}

@media (max-width: 767px) {
  header ul {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: #333;
    z-index: 9999;
  }

  .header-menu,
  .header-close {
    color: white;
    display: flex;
    transform: scale(1.4);
  }

  .header-close {
    position: absolute;
    top: 27px;
    left: 94px;
  }

  header ul.toggle {
    top: 0;
  }
}
