.aboutInfo {
  padding: 30px 80px;
}

.aboutInfo a {
  color: #333;
  text-decoration: none;
}

.aboutInfo .aboutUs {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 80px;
  margin-bottom: 30px;
}

.aboutInfo .aboutUs h1 {
  font-size: 60px;
  margin-bottom: 60px;
}

.aboutInfo .aboutUs ul {
  list-style: none;
  display: flex;
  margin-bottom: 54px;
}

.aboutInfo .aboutUs ul li {
  font-size: 28px;
  padding: 36px;
}

.aboutInfo .aboutUs span {
  font-size: 28px;
  width: 50%;
  text-align: center;
  margin-bottom: 100px;
}

.aboutInfo .aboutCard {
  display: flex;
  justify-content: flex-start;
}

.aboutImg {
  width: 48%;
  object-fit: cover;
}

.aboutInfo .aboutCard h2 {
  font-size: 28px;
  margin-bottom: 40px;
}

.aboutInfo .aboutCard {
  margin-bottom: 80px;
}

.aboutInfo .aboutCard .cardText1 {
  margin: auto 0;
  padding-right: 120px;
}

.aboutInfo .aboutCard .cardText2 {
  margin: auto 0;
  padding-left: 120px;
}

.aboutInfo .aboutCard p {
  font-size: 20px;
  line-height: 1.4;
}

.founder {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 30px;
}

.founder h1 {
  font-size: 28px;
  margin-bottom: 40px;
}

.founder p {
  font-size: 20px;
  width: 63%;
  text-align: center;
  margin-bottom: 100px;
}

.founder img {
  width: 100%;
}

.value {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 30px;
}

.value h1 {
  font-size: 28px;
  margin-bottom: 40px;
}

.value p {
  font-size: 20px;
  width: 63%;
  text-align: center;
  margin-bottom: 60px;
}
