.homeCard {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px auto;
}

.homeCard .box {
  overflow: hidden;
  position: relative;
  margin: 0 20px;
}

.homeCard .box img {
  width: 340px;
  height: 400px;
  overflow: hidden;
  object-fit: cover;
  transition: 0.4s ease-in-out;
  cursor: pointer;
}

.homeCard .box img:hover {
  transform: scale(1.1);
}

.homeCard .box p {
  width: 100%;
  height: 14%;
  background: white;
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0.6;
  cursor: pointer;
}

.homeCard .box span {
  bottom: 18px;
  left: 130px;
  position: absolute;
  color: #333;
  font-size: 18px;
  cursor: pointer;
}

@media (max-width: 767px) {
  .homeCard {
    display: inline-block;
    width: 100%;
  }

  .homeCard .box img {
    width: 100%;
    height: 600px;
  }

  .homeCard .box img:hover {
    transform: none;
  }

  .homeCard .box span {
    bottom: 38px;
    left: 215px;
    position: absolute;
    color: #333;
    font-size: 28px;
  }
}
